.tu-date-editor {
  position: relative;
  display: inline-block;
  text-align: left; }
  .tu-date-editor.tu-input ::selection, .tu-date-editor.tu-input__inner ::selection {
    transition: all 0.3s ease;
    color: #6d5dfc; }
  .tu-date-editor--dates .tu-input__inner {
    text-overflow: ellipsis;
    white-space: nowrap; }
  .tu-date-editor.is-focus {
    box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
  .tu-date-editor .tu-input__icon--suffix {
    cursor: pointer;
    transition: all 0.3s; }
    .tu-date-editor .tu-input__icon--suffix:hover {
      color: #9baacf; }
  .tu-date-editor .tu-range__icon {
    font-size: 14px;
    margin-left: -5px;
    color: #bec8e4;
    float: left;
    line-height: 32px; }
  .tu-date-editor .tu-range-input {
    appearance: none;
    border: none;
    outline: none;
    display: inline-block;
    height: 100%;
    margin: 0;
    padding: 0;
    width: 39%;
    text-align: center;
    font-size: 14px;
    color: #9baacf;
    background: transparent; }
    .tu-date-editor .tu-range-input::placeholder {
      color: #bec8e4; }
  .tu-date-editor .tu-range-separator {
    display: inline-block;
    height: 100%;
    padding: 0 5px;
    margin: 0;
    text-align: center;
    line-height: 32px;
    font-size: 14px;
    width: 5%;
    color: #bec8e4; }
  .tu-date-editor .tu-range__close-icon {
    font-size: 14px;
    color: #bec8e4;
    width: 25px;
    display: inline-block;
    float: right;
    line-height: 32px;
    cursor: pointer; }
    .tu-date-editor .tu-range__close-icon:hover {
      color: #9baacf; }

.tu-date-picker {
  width: 322px;
  box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
  .tu-date-picker.has-sidebar.has-time {
    width: 414px; }
  .tu-date-picker.has-sidebar {
    width: 402px; }
  .tu-date-picker.has-time .tu-picker-panel__body-wrapper {
    position: relative; }
  .tu-date-picker .tu-picker-panel__content {
    width: 292px; }
  .tu-date-picker table {
    table-layout: fixed;
    width: 100%; }
  .tu-date-picker__editor-wrap {
    position: relative;
    display: table-cell;
    padding: 0 5px; }
  .tu-date-picker__time-header {
    position: relative;
    font-size: 12px;
    padding: 8px 5px 5px 5px;
    display: table;
    width: 100%;
    box-sizing: border-box; }
  .tu-date-picker__header {
    margin: 12px;
    text-align: center; }
    .tu-date-picker__header--bordered {
      margin-bottom: 0;
      padding-bottom: 12px; }
      .tu-date-picker__header--bordered + .tu-picker-panel__content {
        margin-top: 0; }
  .tu-date-picker__header-label {
    font-size: 16px;
    font-weight: 500;
    padding: 0 5px;
    line-height: 22px;
    text-align: center;
    cursor: pointer;
    color: #9baacf;
    transition: all 0.3s; }
    .tu-date-picker__header-label:hover {
      color: #6d5dfc; }
    .tu-date-picker__header-label.active {
      color: #6d5dfc; }
  .tu-date-picker__prev-btn {
    float: left; }
  .tu-date-picker__next-btn {
    float: right; }
  .tu-date-picker__time-wrap {
    padding: 10px;
    text-align: center; }
  .tu-date-picker__time-label {
    float: left;
    cursor: pointer;
    line-height: 30px;
    margin-left: 10px; }

.time-select {
  margin: 5px 0;
  min-width: 0; }

.time-select .tu-picker-panel__content {
  margin: 0; }

.time-select-item {
  padding: 8px 10px;
  font-size: 14px;
  line-height: 20px;
  transition: all 0.3s ease;
  color: #9baacf; }

.time-select-item.selected:not(.disabled) {
  color: #6d5dfc; }

.time-select-item:hover:not(.disabled) {
  background: rgba(155, 170, 207, 0.25);
  cursor: pointer; }

.time-select-item.disabled {
  color: #bec8e4;
  cursor: not-allowed; }
